@import url(http://fonts.googleapis.com/earlyaccess/nanumgothiccoding.css);
* {
  font-family: "Nanum Gothic Coding", monospace;
}

html {
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
}

#root {
  width: 100%;
  height: 100%;
}

.content-container {
  margin: 100px 0 50px 0;
}

.content-container:first-child {
  margin-top: 0 !important;
}


.nav-bar {
  background-color: lightgrey;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 75px;
}

.tab-item {
  padding: 5px 10px;
  color: rgba(30, 30, 30, 0.7);
  text-decoration: none;
  font-family: "Nanum Gothic Coding", monospace;
  font-size: 17.5px;
}

.tab-item:hover {
  color: rgba(30, 30, 30, 0.9);
}

.selected {
  color: rgba(30, 30, 30, 1) !important;
}

.logo {
  height: 50px;
  width: 125px;
  object-fit: contain;
}

.button-container {
  margin-right: 10px;
}

.image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  margin-top: 300px;
}

.trigger-container {
  display: flex;
  justify-content: center;
}

.trigger-image {
  height: 500px;
}

.video-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.video-title {
  margin-bottom: 15px;
}

